.input-field {
  height: 40px;
  width: 300px;
  padding: 0 10px;
  margin-left: 10px;
  border: none;
  border-radius: 2px;
  background-color: var(--light);
  font-family: 'Roboto Thin', sans-serif;
  font-size: 16px;
  color: var(--dark); }
  .input-field::-webkit-input-placeholder {
    font-family: 'Roboto Thin', sans-serif;
    font-size: 16px;
    color: var(--dark); }
  .input-field:-ms-input-placeholder {
    font-family: 'Roboto Thin', sans-serif;
    font-size: 16px;
    color: var(--dark); }
  .input-field::-ms-input-placeholder {
    font-family: 'Roboto Thin', sans-serif;
    font-size: 16px;
    color: var(--dark); }
  .input-field::placeholder {
    font-family: 'Roboto Thin', sans-serif;
    font-size: 16px;
    color: var(--dark); }



