.input-line {
  width: 300px;
  padding: 5px;
  border: none;
  border-bottom: solid 2px var(--light);
  font-family: 'Roboto Light', sans-serif;
  font-size: 16px;
  color: var(--light);
  background-color: var(--dark); }
  .input-line::-webkit-input-placeholder {
    font-family: 'Roboto Light', sans-serif;
    font-size: 16px;
    color: var(--light); }
  .input-line:-ms-input-placeholder {
    font-family: 'Roboto Light', sans-serif;
    font-size: 16px;
    color: var(--light); }
  .input-line::-ms-input-placeholder {
    font-family: 'Roboto Light', sans-serif;
    font-size: 16px;
    color: var(--light); }
  .input-line::placeholder {
    font-family: 'Roboto Light', sans-serif;
    font-size: 16px;
    color: var(--light); }



