/*-----Imports------*/
@font-face {
  font-family: 'Roboto Thin';
  src: url("./fonts/Roboto-Thin.ttf"); }

@font-face {
  font-family: 'Roboto Medium';
  src: url("./fonts/Roboto-Medium.ttf"); }

@font-face {
  font-family: 'Roboto Light';
  src: url("./fonts/Roboto-Light.ttf"); }

@font-face {
  font-family: 'Cardenio Modern';
  src: url("./fonts/Cardenio-Modern-Bold.ttf"); }

@font-face {
  font-family: 'icomoon';
  src: url("./fonts/icomoon.eot");
  src: url("./fonts/icomoon.eot") format("embedded-opentype"), url("./fonts/icomoon.ttf") format("truetype"), url("./fonts/icomoon.woff") format("woff"), url("./fonts/icomoon.svg") format("svg");
  font-weight: normal;
  font-style: normal; }

/*-----Variables-----*/
/*Variables don't work here, the custom properties for
colors can be found in index.scss*/
body {
  background-color: var(--dark); }

/*-----Color classes-----*/
.dark {
  color: var(--dark); }

.bg-dark {
  background-color: var(--dark); }

.light {
  color: var(--light); }

.bg-light {
  background-color: var(--light); }

.search {
  color: var(--search); }

.bg-search {
  background: var(--search); }

.find {
  color: var(--find); }

.bg-find {
  background-color: var(--find); }

/*-----Font classes-----*/
.hd-25 {
  font-family: "Cardenio Modern", sans-serif;
  font-size: 25px; }

.hd-20 {
  font-family: "Cardenio Modern", sans-serif;
  font-size: 20px; }

.text-20-thin {
  font-family: "Roboto Thin", sans-serif;
  font-size: 16px; }

.text-16-thin {
  font-family: "Roboto Thin", sans-serif;
  font-size: 16px; }

.text-14-light {
  font-family: "Roboto Light", sans-serif;
  font-size: 14px; }

.text-14-med {
  font-family: "Roboto Medium", sans-serif;
  font-size: 14px; }

/*-----Flex Box-----*/
.flex {
  position: absolute;
  top: 80px;
  display: -ms-flexbox;
  display: flex; }

.row {
  -ms-flex-direction: row;
      flex-direction: row; }

.column {
  -ms-flex-direction: column;
      flex-direction: column; }

.ji-space-around {
  -ms-flex-pack: distribute;
      justify-content: space-around; }

.ai-middle {
  -ms-flex-align: center;
      align-items: center; }

/*-----Others-----*/
.m {
  margin: 10px; }

/*-----Icons-----*/
[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

